import React, { Component } from 'react'
import B from "./images/B.png"

export default class ModuleFiveB extends Component {

    render() {
        return (
            <>
                <div class="module-page-title" id="moduleonename">
                    B. PASIF, AGRESIF, DAN ASERTIF
                </div>
                <div class="module-text" id="moduleonetext">
                    <img class="moduleimgwide" src={B} />
                    <br /><br />
                    Tidak jarang orang keliru menyamakan asertif dengan agresif, atau kita sering melihat kedua hal ini hampir sama saja. Asertif dan agresif memiliki tujuan untuk menyampaikan pesan dengan jelas. Hanya saja agresif cenderung dan berpotensi untuk menyakiti dan mempermalukan orang lain. Asertif adalah keseimbangan dari agresif dan pasif, di saat yang sama asertif menjadi kontra produktif dari sikap agresif sekaligus pasif. Sementara juga ada sikap “plin-plan” yang cenderung bersikap tampak pasif namun sebenarnya agresif.
                    <br /><br />
                    Keuntungan berperilaku asertif, dengan menyatakan apa adanya perasaan atau emosinya seseorang tidak akan dikendalikan orang lain, efektif dalam berinteraksi, lebih dihargai orang lain, menjadi lebih percaya diri dan memiliki rasa puas. Dengan tabel berikut ini kita akan dapat melihat perbedaannya:
                    <br /><br />
                    <div class="module-subtitle2">Asertif</div>
                    <ul>
                        <li>Ekspresi dirinya disampaikan dengan menghormati orang lain.</li>
                        <li>Tujuannya tidak hanya untuk dirinya sendiri, tetapi juga demi kebaikan bersama dengan mencari cara untuk mendapatkan kesepakatan yang adil tanpa merugikan siapa pun bahkan menginginkan yang terbaik bagi orang lain</li>
                        <li>Sikap ini juga menunjukkan bagaimana menghormati dirinya sendiri.</li>
                        <li>Mampu berkomunikasi secara efektif dan terbuka untuk menyampaikan hal-hal yang positif (seringkali dalam beberapa kasus juga dalam rangka mengembalikan haknya yang terampas oleh orang yang berinteraksi dengannya) sehingga ia dapat menjaga martabatnya tanpa perlu meruntuhkan martabat orang lain.</li>
                    </ul>
                    <div class="module-subtitle2">Pasif</div>
                    <ul>
                        <li>Sikap dan perilakunya mungkin tampak seperti pemalu atau easygoing. Padahal sebenarnya mereka tidak tahu bagaimana mengomunikasikan perasaannya dengan tepat kepada orang lain.</li>
                        <li>Mereka cenderung menghindari dan takut menghadapi konflik yang akan timbul bila mereka mengekspresikan pikiran dan perasaannya secara terbuka.</li>
                        <li>Mereka kompromi demi “menjaga kedamaian” dengan mengorbankan diri, membiarkan kebutuhan mereka tak terpenuhi, haknya dilanggar, dan menutupi perasaannya agar "kedamaian terpelihara". Pada kenyataan sebenarnya adalah merasa bahwa pikiran dan perasaan pribadinya tidak terlalu penting sebagaimana orang lain.</li>
                        <li>Mereka membiarkan orang lain menang dan membiarkan dirinya kalah. Padahal kenyataannya semua pihak yang terlibat sebenarnya kalah, setidaknya sampai pada taraf tertentu.</li>
                        <li>Sering tidak ada perlawanan, tidak berani berkata tidak sekalipun sadar tidak sanggup untuk memenuhi permintaan orang lain, tidak berani mengungkapkan perasaannya dan memperjuangkan hak pribadinya.</li>
                        <li>Seringkali mengalami konflik (batin) secara pribadi karena tidak berani mengambil keputusan tegas bahkan yang berkaitan dengan haknya.</li>
                    </ul>
                    <div class="module-subtitle2">Agresif</div>
                    <ul>
                        <li>Tindakannya cenderung ingin menyerang sesuatu yang dipandang sebagai hal atau situasi yang mengecewakan, menghalangi, atau menghambat dengan memaksakan suatu hal tanpa memikirkan kebaikan bagi orang lain tersebut.</li>
                        <li>Mereka menggunakan tekanan dan manipulasi untuk mencari keuntungan diri sendiri. Mereka meraih kemenangan dengan cara apapun; mengintimidasi, menindas secara fisik bahkan dengan mengorbankan orang lain dan menciptakan konflik yang tidak perlu.</li>
                        <li>Tidak menghargai hak orang lain, merendahkan, kasar, kejam, menghina, bahkan menindas sehingga mengakibatkan mereka kehilangan martabat. Mereka mendapatkan apa yang menjadi keinginannya tetapi kehilangan penghargaan dan bahkan tidak hanya dihindari tetapi juga mendapatkan perlawanan.</li>
                        <li>Mereka memiliki asumsi yang negatif terhadap motivasi orang lain dan selalu berpikir untuk membalas. Mereka tidak berpikir dengan sudut pandang orang lain.</li>
                    </ul>
                    <div class="module-subtitle2">Pasif - Agresif</div>
                    <ul>
                        <li>Sikap ini tidak jelas. Secara tampilan tampak pasif, namun sebenarnya agresif (dari belakang). Terkadang berkata ya, padahal sebenarnya ingin berkata tidak. Sering bersikap kasar, marah dan menunjukkan sikap yang negatif atau mengeluh tentang orang lain di belakangnya, tetapi tidak berani secara langsung berhadapan dan menyelesaikan masalahnya.</li>
                        <li>Jika sikap ini terus menerus dikembangkan maka akan merusak hubungan dan kehilangan penghargaan sehingga menyulitkan diri untuk mencapai sasaran atau kebutuhan.</li>
                    </ul>
                </div>
            </ >
        )
    }
}