import React, { Component } from 'react'
import B from "./images/B.png"
import B2 from "./images/B2.png"

export default class ModuleEightB extends Component {

    render() {
        return (
            <>
                <div class="module-page-title" id="moduleonename">
                    B. MENGELOLA PELUANG
                </div>
                <div class="module-text" id="moduleonetext">
                    Sebagaimana disampaikan sebelumnya, peluang harus dikelola agar dapat dimanfaatkan untuk perbaikan kualitas hidup. Mengelola peluang adalah proses menemukenali, menentukan dan menindaklanjuti kemungkinan yang dapat membawa pemuda menciptakan kualitas kehidupan yang lebih baik melalui perolehan kerja layak. Sehingga dari definisi tersebut dapat diambil langkah-langkah pengelolaan peluang sebagai berikut:
                    <ol>
                        <li class="module-subtitle2">Menemukenali kesempatan peningkatan kapasitas personal</li>
                        Langkah pertama yang harus dilakukan adalah menemukenali kesempatan yang bertujuan untuk meningkatkan kapasitas kerja/wirausaha para pemuda. Hal ini bisa dilakukan melalui.
                        <br /><br />
                        <b><i>Searching</i></b>
                        <br />
                        Merupakah proses pencarian kesempatan peningkatan kapasitas, di mana hal tersebut dapat diperoleh melalui:
                        <ul>
                            •Pencarian virtual, yaitu dengan menggunakan teknologi internet untuk mencari informasi pelatihan peningkatan kapasitas kerja/wirausaha.<br />
                            •Pencarian agensi, yaitu proses mencari informasi melalui institusi pemerintah terkait. Dalam hal ini pemuda bisa berkunjung ke Dinas Tenaga Kerja atau Dinas UMKM atau balai Latihan Kerja Pemerintah untuk mencari informasi apakah mereka menyediakan pelatihan-pelatihan terkait peningkatan kapasitas kerja/wirausaha bagi pemuda.<br />
                            •Pencarian fasilitator, yaitu proses mencari informasi melalui lembaga non-pemerintah terkait program-program pelatihan peningkatan kapasitas atau kesiapan kerja. Dalam model pencarian ini para pemuda harus berhati-hati untuk menghindari hal yang tidak diinginkan seperti penipuan. Perlu mencari lembaga yang kredibel dan telah memiliki rekan jejak yang baik.<br />
                        </ul>
                        <br /><br />
                        <b><i>Compiling</i></b>
                        <br />
                        Merupakan proses menyatukan informasi yang didapatkan melalui proses searching. Hal ini untuk memudahkan pemuda dalam mendeskripsikan dan membandingkan setiap pilihan kesempatan. Tabel bantu berikut dapat digunakan pada proses ini:
                        <img src={B} style={{ width: '100%' }} />
                        <br /><br />
                        <b><i>Matching</i></b>
                        <br />
                        Merupakan proses memilih opsi kesempatan yang sesuai dengan kebutuhan setiap pemuda. Dalam hal ini pemuda harus menentukan elemen kunci pertimbangan yang digunakan untuk mencocokkan opsi yang paling sesuai. Pertimbangan setiap pemuda mungkin akan berbeda.
                        <br /><br />
                        <span style={{ color: "#40A26E"}}>Contoh:</span> Seorang pemuda menentukan faktor kunci bagi dirinya adalah adanya proses pendampingan pada saat dan setelah pelatihan, karena pemuda membutuhkan diskusi lanjutan. Dengan contoh tabel sebelumnya maka kemudian pemuda melihat kembali dan memilih opsi ketiga karena matching atau sesuai dengan kebutuhannya.
                        <img src={B2} style={{ width: '100%' }} />
                        Warna hijau menunjukkan opsi yang dipilih pemuda dalam meingkatkan kapasitas kerja karena opsi tersebut dapat menjawab kebutuhan pemuda terkait pendampingan.
                        <br /><br />
                        <li class="module-subtitle2">Menindaklanjuti pilihan peningkatan kapasitas</li>
                        Langkah selanjutnya yang harus dilakukan pemuda adalah menindaklanjuti pilihan yang telah diambil. Proses ini merupakan upaya nyata dalam mewujudkan peningkatan kapasitas.
                        <br /><br />
                        <span style={{ color: "#40A26E"}}>Contoh:</span> Ketika pemuda telah memilih opsi ketiga pada tabel sebelumnya maka kemudian pemuda harus menindaklanjutinya dengan:
                        <ul>
                            <ol>Mendaftar atau melakukan registrasi di lembaga penyedia layanan</ol>
                            <ol>Mengikuti proses pelatihan sesuai ketentuan</ol>
                            <ol>Memanfaatkan layanan pendampingan secara relevan untuk peningkatan kapasitas. </ol>
                        </ul>
                        <br /><br />
                        <li class="module-subtitle2">Menemukenali peluang kerja/wirausaha</li>
                        Langkah yang dilakukan mirip dengan langkah-langkah yang telah disampaikan pada point pertama, namun bahan pertimbangannya adalah skill/kapasitas yang telah dimiliki dari proses peningkatan kapasitas yang dilakukan sebelumnya.
                        <br /><br />
                        <b><i>Searching kesempatan kerja/wirausaha</i></b>
                        <br />
                        Dalam tahap ini bagi pemuda yang memiliki minat bekerja proses searching dilakukan untuk menemukan peluang kerja melalui portal informasi kerja, Disnaker setempat atau kunjungan ke websiteresmi perusahaan. Sedangkan bagi pemuda yang memiliki minat usaha, akan melakukan pemetaan peluang bisnis yang teknisnya akan disampaikan pada pembelajaran di tingkat lanjut.
                        <br /><br />
                        <b><i>Compiling kesempatan kerja/wirausaha</i></b>
                        Dalam tahap ini pemuda menyatukan informasi yang didapatkan dari proses searching, misalnya bagi pemuda dengan minat bekerja akan menemukan lowongan kerja di 3 perusahaan, sedangkan bagi pemuda yang minat wirausaha akan menemukan 3 opsi jenis usaha.
                        <br /><br />
                        <b><i>Matching kesempatan kerja/wirausaha</i></b>
                        Dalam tahap ini pemuda menentukan opsi yang akan diambil dari pilihan yang tersedia. Dalam contoh kasus pemuda dengan peminatan bekerja akan memilih 1 dari 3 lowongan yang ditemukan, sedangkan bagi pemuda dengan peminatan wirausaha akan memilih 1 dari 3 peluang bisnis yang ditemukenali.
                        <br /><br />
                        <li class="module-subtitle2">Menindaklanjuti kesempatan kerja/wirausaha</li>
                        Seperti halnya pada langkah sebelumnya, ketika pilihan telah diambil maka perlu dilakukan langkah lanjutan untuk mewujudkan manfaat dari opsi yang diambil dalam mewujudkan tujuan pemuda untuk meningkatkan kualitas hidupnya.
                        <br /><br />
                        Dari simulasi yang disampaikan, maka dapat dicontohkan ketika pemuda dengan minat bekerja telah memilih opsi lowongan di Perusahaan A, maka selanjutnya pemuda tersebut harus menindaklanjutinya dengan mengirimkan berkas lamaran, mengikuti seleksi dan bekerja. Sedangkan bagi pemuda yang telah memilih salah satu opsi jenis bisnis, maka harus ditindaklanjuti dengan melakukan set-up bisnis dan mengoperasionalkan bisnis tersebut.
                    </ol>
                </div>
            </ >
        )
    }
}